@import '../../sass/main.scss';

.accomodation {
  &_content {
    @include flex(row, space-between, flex-start);
    color: $color-secondary;
    width: 95%;
    margin: 0 auto;
    @media screen and (max-width: 650px) {
      @include flex(column, space-between, flex-start);   
      gap: 10px;   
    }
    &_infos {
      display: flex;
      flex-direction: column;
      gap: 10px;
      font-weight: 500;
      font-size: 14px;
      button {
        display: inline;
        padding: 5px 15px;
        margin-right: 10px;
        cursor: pointer;
        border-radius: 10px;
        border: none;
        color: $color-primary;
        background-color: $color-secondary;
        &:hover {
          box-shadow: 0 2px 8px rgb(0, 0, 0);
        }
      }
      div {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;  
      }
      .tag-container {
        display: flex;
        flex-wrap: wrap;
        margin-top: 1rem;
        gap: 5px;
      }
      
      .tag {
        padding: 0.5rem;
        border-radius: 10px;
      }
      
    }
    &_host {
      display: flex;
      flex-direction: column;
      gap: 5px;
      @media screen and (max-width: 650px) {
        width: 100%;
        @include flex(row-reverse, space-between, center);     
      }
      div {
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        gap: 0 10px;
        @media screen and (max-width: 500px) {
          gap: 0 5px;   
        }
      }
      &_name {
        align-self: center;
        flex-direction: column;
      }
      img {
        width: 64px;
        height: 64px;
        object-fit: cover;
        object-position: center;
        border-radius: 50%;
      }
      &_stars {
        display: flex;
        gap: 10px;
        img {
          width: 30px;
          height: 30px;
          @media screen and (max-width: 650px) {
            width: 20px;
            height: 20px;  
          }
        }
      }
    }
  }
}
